.AttributeItem {
    width: 100px;
    height: 100px;
    margin: 5px;
    background: #ACACAC;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.AttributeItem:hover {
    cursor: pointer;
}

.AttributeItem--selected {
    background: #FFFFFF;
}

.AttributeItem__image {
    max-width: none;
    width: 100%;
}
.AttributeSelection {
    width: 100%;
    display: flex;
}
.AttributeColorSection {
    display: flex;
    width: 100%;
    flex-direction: column;
    margin: 8px 0;
}

.AttributeColorSection__title {
    font-size: 1.2rem;
    font-family: 'Poppins', sans-serif;
    margin: 10px 0;
    color: #808183;
}

.twitter-picker {
    width: 100% !important;
}

.twitter-picker div span div {
    border: 2px solid #b1b0b0 !important;
    margin: 0 3px 3px 0 !important;
 }
.AttributeTypeItem {
    width: 80px;
    height: 80px;
    border: 2px solid #808183;
    border-radius: 10px;
    margin: 2px;
    padding: 0;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.AttributeTypeItem:hover {
    cursor: pointer;
}

.AttributeTypeItem__check {
    display: inline;
    z-index: 1;
}

.AttributeTypeItem__check--remove {
    display: none;
}

.AttributeTypeItem__image {
    max-width: none;
    width: 100%;
    position: absolute;
}

.AttributeTypeSection {
    display: flex;
    flex-direction: column;
}

.AttributeTypeSection__title {
    font-size: 1.2rem;
    font-family: 'Poppins', sans-serif;
    margin: 10px 0;
    color: #808183;
}

.AttributeTypeSection__items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.FilterSection {
    display: flex;
    flex-direction: column;
    margin: 8px 0;
}

.FilterSection__title {
    font-size: 1.2rem;
    font-family: 'Poppins', sans-serif;
    margin: 10px 0;
    color: #808183;
}

.FilterSection__select {
    border: none;
    border-bottom: 2px solid #808183;
    font-family: 'Poppins', sans-serif;
    font-size: 1.125rem;
    outline: none;
    color: #808183;
}
.AttributeOptions {
    display: flex;
    padding: 20px 30px;
    flex-direction: column;
    border-radius: 20px;
    background: #FFFFFF;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.25);
    margin: 5px;    
}

.AttributeOptions__title {
    font-family: 'Poppins', sans-serif;
    font-size: 1.3rem;
    color: #808183;
    text-align: center;
    margin: 5px 0;
}
.EditPanel {
    background-color: rgb(28, 28, 39);
    padding: 5px 10px;
    width: 100%;
    max-width: 590px;
    z-index: 1;
}
.CatControl {
    padding: 20px 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    background: #FFFFFF;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.25);
    margin: 10px 5px;
}

.CatControl__options {
    margin: 0;
    width: 100%;
}

.CatControl__section {
    margin: 10px 0;
    width: 100%;
    display: flex;
}

.CatControl__input {
    width: 100%;
    max-width: 500px;
    margin-top: 5px;
    margin-bottom: 10px;
    border: none;
    border-bottom: 2px solid #808183;
    font-family: 'Poppins', sans-serif;
    font-size: 1.125rem;
    outline: none;
    color: #808183;
}

.CatControl__input::-webkit-input-placeholder {
    color: #9e9e9e;
}

.CatControl__input:-ms-input-placeholder {
    color: #9e9e9e;
}

.CatControl__input::placeholder {
    color: #9e9e9e;
}

.CatControl__input:focus {
    border-color: #4ABCAD;
}

.CatControl__btn {
    width: 100%;
    padding: 8px 5px;
    border: none;
    border-radius: 10px;
    text-decoration: inherit;
    color: white;
    font-family: 'Poppins', sans-serif;
    font-size: 1.125rem;
    margin: 0 5px;
    outline: none;
    text-align: center;
}

.CatControl__btn {
    cursor: pointer;
}

.CatControl__btn--green {
    background-color: #4ABCAD;
}

.CatControl__btn--default {
    background-color: #808183;
}

.CatControl__btn--navegate {
    background-color: rgb(28, 28, 39);
}

.CatControl__btn--exit {
    background-color: #FC4A1A;
}
.Creator {
    display: flex;
    width: 100%;
    height: 100%;
    background-color: #9FD1FF;
    position: relative;
}
.CatView {
    transition: all .5s ease;
    width: 100%;
    max-width: none;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.CatView__container {
    width: 100%;
    max-width: none;
    height: 95%;
}

.CatView--eyes {
    transform: scale(3.5) translate(-50px, 140px);
    transition: all .6s ease-out;
}

.CatView--necklace {
    transform: scale(3) translate(-50px, 40px);
    transition: all .5s ease-out;
}

.CatView--hat {
    transform: scale(3) translate(-50px, 230px);
    transition: all .5s ease-out;
}

.EditView {
    width: 100%;
    max-width: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    background-color: #9FD1FF;
}
.Intro {
    background-color: rgb(28, 28, 39);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Intro__title {
    margin: 0;
    font-size: 6rem;
    color: white;
    font-family: 'Lobster', cursive;
}

.Intro__container {
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 100%;
    flex-direction: column;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Intro__controls {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
}

.Intro__btn {
    background-color: #4ABCAD;
    padding: 10px 15px;
    border-radius: 10px;
    text-decoration: inherit;
    color: white;
    font-family: 'Poppins', sans-serif;
    font-size: 1.5rem;
    margin: 10px 2px;
    text-align: center;
}

.Intro__btn--gray {
    background-color: #808183;
}
.CatItem {
    padding-right: 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 200px;
    width: 95%;
    list-style-type: none;
    margin-top: 10px;
    background-color: teal;
    text-decoration: none;
    border-radius: 25px;
}

.CatItem__preview {
    width: 200px;
    border-radius: 25px 0 0 25px;
    overflow: hidden;
    height: 100%;
    max-width: none;
    display: flex;
    align-items: center;
    margin-right: 10px;
}

.CatItem__preview--big {
    width: 1200px;
    position: fixed;
    left: 0;
    top: 0;
    border-radius: 0;
}

.CatItem__info {
    display: flex;
    width: 100%;
    flex-direction: column;
    font-family: 'Poppins', sans-serif;
    color: white;
    margin-left: 10px;
}

.CatItem__info h3 {
    margin: 3px 0;
    font-size: 1.5rem;
    font-weight: bold;
}

.CatItem__info p {
    margin: 3px 0;
}

.CatItem__control {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.CatItem__btn {
    width: 150px;
    padding: 8px 0px;
    border: none;
    border-radius: 10px;
    text-decoration: inherit;
    color: #4ABCAD;
    font-family: 'Poppins', sans-serif;
    font-size: 1.125rem;
    margin: 5px 0px;
    outline: none;
    text-align: center;
    background-color: white;
    font-weight: 500;
}

.CatItem__btn:hover {
    cursor: pointer;
}

.CatItem__btn--delete {
    background-color: #FC4A1A;
    color: white;
}

.CatItem__btn--download {
    background-color: rgb(28, 28, 39);
    color: white;
}
.CreatedCats {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(28, 28, 39);
    padding: 0;
}

.CreatedCats__title {
    font-family: 'Poppins', sans-serif;
    font-size: 2.4rem;
    font-weight: bold;
    color: white;
}

.CreatedCats__container {
    width: 98%;
    list-style-type: none;
    margin: 10px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.CreatedCats__newBtn {
    width: 100%;
    padding: 8px 5px;
    border: none;
    border-radius: 10px;
    text-decoration: inherit;
    color: white;
    font-family: 'Poppins', sans-serif;
    font-size: 1.125rem;
    margin: 0 5px;
    outline: none;
    text-align: center;
    background-color: #4ABCAD;
    max-width: 300px;
}

/*
	HTML5 Reset :: style.css
	----------------------------------------------------------
	We have learned much from/been inspired by/taken code where offered from:

	Eric Meyer					:: https://meyerweb.com
	HTML5 Doctor				:: https://html5doctor.com
	and the HTML5 Boilerplate	:: https://html5boilerplate.com

-------------------------------------------------------------------------------*/

/* Let's default this puppy out
-------------------------------------------------------------------------------*/

html, body, body div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, figure, footer, header, menu, nav, section, time, mark, audio, video, details, summary {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font-weight: normal;
	vertical-align: baseline;
	background: transparent;
}

article, aside, figure, footer, header, nav, section, details, summary {display: block;}

/* Handle box-sizing while better addressing child elements:
   https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	box-sizing: inherit;
}

/* consider resetting the default cursor: https://gist.github.com/murtaugh/5247154 */

/* Responsive images and other embedded objects
*/
img,
object,
embed {max-width: 100%;}

/*
   Note: keeping IMG here will cause problems if you're using foreground images as sprites.
	 In fact, it *will* cause problems with Google Maps' controls at small size.
	If this is the case for you, try uncommenting the following:

#map img {
		max-width: none;
}
*/

/* force a vertical scrollbar to prevent a jumpy page */
html {overflow-y: scroll;}

/* we use a lot of ULs that aren't bulleted.
	don't forget to restore the bullets within content. */
ul {list-style: none;}

blockquote, q {quotes: none;}

blockquote:before,
blockquote:after,
q:before,
q:after {content: ''; content: none;}

a {margin: 0; padding: 0; font-size: 100%; vertical-align: baseline; background: transparent; text-decoration: none;}

del {text-decoration: line-through;}

abbr[title], dfn[title] {border-bottom: 1px dotted #000; cursor: help;}

/* tables still need cellspacing="0" in the markup */
table {border-collapse: collapse; border-spacing: 0;}
th {font-weight: bold; vertical-align: bottom;}
td {font-weight: normal; vertical-align: top;}

hr {display: block; height: 1px; border: 0; border-top: 1px solid #ccc; margin: 1em 0; padding: 0;}

input, select {vertical-align: middle;}

pre {
    white-space: pre; /* CSS2 */
    white-space: pre-wrap; /* CSS 2.1 */
    white-space: pre-line; /* CSS 3 (and 2.1 as well, actually) */
    word-wrap: break-word; /* IE */
}

input[type="radio"] {vertical-align: text-bottom;}
input[type="checkbox"] {vertical-align: bottom;}
.ie7 input[type="checkbox"] {vertical-align: baseline;}
.ie6 input {vertical-align: text-bottom;}

select, input, textarea {font: 99% sans-serif;}

table {font-size: inherit; font: 100%;}

small {font-size: 85%;}

strong {font-weight: bold;}

td, td img {vertical-align: top;}

/* Make sure sup and sub don't mess with your line-heights https://gist.github.com/413930 */
sub, sup {font-size: 75%; line-height: 0; position: relative;}
sup {top: -0.5em;}
sub {bottom: -0.25em;}

/* standardize any monospaced elements */
pre, code, kbd, samp {font-family: monospace, sans-serif;}

/* hand cursor on clickable elements */
.clickable,
label,
input[type=button],
input[type=submit],
input[type=file],
button {cursor: pointer;}

/* Webkit browsers add a 2px margin outside the chrome of form elements */
button, input, select, textarea {margin: 0;}

/* make buttons play nice in IE */
button,
input[type=button] {width: auto; overflow: visible;}

/* scale images in IE7 more attractively */
.ie7 img {-ms-interpolation-mode: bicubic;}

/* prevent BG image flicker upon hover
   (commented out as usage is rare, and the filter syntax messes with some pre-processors)
.ie6 html {filter: expression(document.execCommand("BackgroundImageCache", false, true));}
*/

/* let's clear some floats */
.clearfix:before, .clearfix:after { content: "\0020"; display: block; height: 0; overflow: hidden; }
.clearfix:after { clear: both; }
.clearfix { zoom: 1; }

